.header {
  text-align: left;
  padding-top: 10px;
  background-color: #ffffff; }
  .header .header__title {
    font-family: 'Exo 2', 'Arial Narrow', sans-serif;
    font-size: 54px;
    line-height: 120%;
    font-weight: 800;
    color: #000000; }
  .header .header__subtitle {
    font-family: 'Roboto', 'Arial Narrow', sans-serif;
    font-size: 21px;
    line-height: 120%;
    font-weight: 700;
    color: #019ce6;
    margin-bottom: 20px; }
  .header .header__info {
    background-color: #b3b3b3; }
    .header .header__info-body {
      display: flex;
      align-items: center;
      justify-content: space-between; }
  .header .header__contacts {
    display: flex;
    gap: 40px;
    margin: 15px 0 15px 30px;
    font-size: 16px;
    font-weight: 500;
    line-height: 150%; }
  .header .header__email,
  .header .header__phone,
  .header .header__adress {
    position: relative; }
    .header .header__email::before,
    .header .header__phone::before,
    .header .header__adress::before {
      content: '';
      display: block;
      width: 12px;
      height: 12px;
      border-radius: 6px;
      background-color: #ffffff;
      position: absolute;
      top: 6px;
      left: -25px; }
    .header .header__email:hover::before,
    .header .header__phone:hover::before,
    .header .header__adress:hover::before {
      background-color: #017ab3; }

@media screen and (max-width: 767px) {
  .header {
    text-align: center; }
    .header .header__title {
      font-size: 34px; }
    .header .header__subtitle {
      font-size: 18px;
      margin-bottom: 10px; }
    .header .header__contacts {
      flex-direction: column;
      gap: 10px;
      margin: 15px 0 15px 0px; }
    .header .header__info-body {
      flex-direction: column;
      align-items: center; }
    .header .header__email::before,
    .header .header__phone::before,
    .header .header__adress::before {
      display: none; } }

.social {
